import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnExport } from 'components/common/button';
import { Card, Row, Col, message } from 'antd';
import { isEmpty } from 'lodash';
import { CartHeadAction, TableData } from 'components/common';
import {
  getTuititionDeptStatistic,
  exportStudentFeeDeptReport,
  exportClassFeeDebtReport,
  exportStudentFeeDeptByReceiptReport,
} from 'services/tuititionDeptStatistic';
import FormSearchStudent from './FormSearchStudent';
import { StatisticBar } from './StatisticBar';
import { getColumConfig } from './tableConfig';
import './style.scss';

class TuitionDeptStatisticPage extends Component {
  state = {
    reload: 0,
    paramSearchClass: {},
    statistics: {},
    isEmptyData: true,
    isSearching: false,
    isExportingStudentDebtByClass: false,
    isExportingClassDebt: false,
    isExportingDebtByReceipt: false
  };

  onViewRegistedCredits = () => {
    console.log("View Registed Credits");
  }
  
  dataFormSeach = data => {
    this.setState({
      paramSearchClass: data,
      reload: this.state.reload + 1
    });
  };

  handleExportStudentDebtByClass = async() => {
    const { paramSearchClass } = this.state;
    this.setState({ isExportingStudentDebtByClass: true });
    try {
      await exportStudentFeeDeptReport(paramSearchClass)
    } catch(err) {
      message.error('Có lỗi xãy ra');
    }
    this.setState({ isExportingStudentDebtByClass: false });
  }

  handleExportClassDebtReport = async() => {
    const { paramSearchClass } = this.state;
    this.setState({ isExportingClassDebt: true });
    try {
      await exportClassFeeDebtReport(paramSearchClass)
    } catch(err) {
      message.error('Có lỗi xãy ra');
    }
    this.setState({ isExportingClassDebt: false });
  }

  handleExportStudentDebByReceipt = async() => {
    const { paramSearchClass } = this.state;
    this.setState({ isExportingDebtByReceipt: true });
    try {
      await exportStudentFeeDeptByReceiptReport(paramSearchClass)
    } catch(err) {
      message.error('Có lỗi xãy ra');
    }
    this.setState({ isExportingDebtByReceipt: false });
  }

  handleSearch = async(params) => {
    this.setState({ isSearching: true });
    try {
      const result= await getTuititionDeptStatistic(params);
      if(result.data && !isEmpty(result.data.items)) {
        this.setState(
          {
            statistics: result.data.information,
            isEmptyData: false
          }
        );
      } else {
        this.setState({
          statistics: {},
          isEmptyData: true
        });
      }
      this.setState({ isSearching: false });
      return result;
    } catch(err) {
      message.error('Có lỗi xãy ra');
      this.setState({ isSearching: false });
      return {}
    }
  }

  render() {
    const {
      reload,
      paramSearchClass,
      statistics,
      isEmptyData,
      isSearching,
      isExportingStudentDebtByClass,
      isExportingClassDebt,
      isExportingDebtByReceipt
    } = this.state;
    const { yearEduId, semesterId } = paramSearchClass;
    const isBtnViewVisible = true;
    const isExportDisable = isEmptyData || isSearching;
    

    return (
      <Card title="DS lớp học">
        <CartHeadAction>
          <div className="btn-group">
            <BtnExport
              className="btnExport"
              title="Xuất báo cáo SV nợ theo lớp"
              disabled={isExportDisable}
              loading={isExportingStudentDebtByClass}
              onClick={this.handleExportStudentDebtByClass}
            />
            <BtnExport
              className="btnExport"
              title="Xuất báo cáo nợ theo lớp"
              disabled={isExportDisable}
              loading={isExportingClassDebt}
              onClick={this.handleExportClassDebtReport}
            />
            <BtnExport
              className="btnExport"
              title="Xuất SV nợ theo biên lai"
              disabled={isExportDisable}
              loading={isExportingDebtByReceipt}
              onClick={this.handleExportStudentDebByReceipt}
            />
          </div>
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <FormSearchStudent dataForm={this.dataFormSeach} isBtnViewVisible={isBtnViewVisible} />
          </Col>
        </Row>
        <StatisticBar {...statistics} />
        <Row>
          <Col span={24}>
            <TableData
              reload={reload}
              service={this.handleSearch}
              paramSearch={paramSearchClass}
              columns={getColumConfig(this)}
              allowGetData={!!yearEduId && !!semesterId}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <TuitionDeptStatisticPage />
  </Page>
);



